<template>
  <div class="hot">
    <div class="hottop">
      <div class="hottopwrap">
        <div class="hoticon"></div>
        <div class="hottime">
          更新时间 {{ String(dateTime.split("/")[1]).padStart(2, "0") }}月{{
            String(dateTime.split("/")[2]).slice(0, 2)
          }}日
        </div>
      </div>
    </div>
    <ul class="hot-song">
      <HotItem
        v-for="(song, index) in hotlists"
        :key="song.id"
        :hotlists="hotlists"
        :song="song"
        :index="index"
        @updata-song="$emit('updata-song', $event)"
        @get-playlist="$emit('get-playlist', $event)"
      />
    </ul>
    <div class="hotdown">
      <span> 查看完整榜单 </span>
    </div>
  </div>
</template>

<script>
import HotItem from "@/components/HotItem.vue";

export default {
  components: {
    HotItem,
  },
  data() {
    return {
      hotlists: [],
      dateTime: "",
    };
  },

  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        this.hotlists = res.data.playlist.tracks.slice(0, 20);
        // console.log(new Date(res.data.playlist.updateTime).toLocaleString()); //2022/9/20 08:15:32
        this.dateTime = new Date(res.data.playlist.updateTime).toLocaleString();
      });
  },
};
</script>

