<template>
  <li
    class="song-info"
    @click="
      $emit('updata-song', song);
      $emit('get-playlist', hotlists);
    "
  >
    <div class="rank">{{ String(index + 1).padStart(2, "0") }}</div>
    <div class="textdec">
      <div class="left">
        <p>{{ song.name }}</p>
        <p>
          <i v-if="song.sq !== null"></i>
          <!-- 判断是不是有SQ song.sqmusic === null -->
          {{ song.ar[0].name }}-{{ song.name }}
        </p>
      </div>
      <div class="right">
        <span></span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    index: Number,
    hotlists: Array,
  },
};
</script>

<style lang="less" scoped>
.song-info {
  width: 375rem;
  box-sizing: content-box;
  display: flex;
  .rank {
    width: 40rem;
    font-size: 17rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    .rank {
      color: #df3436;
    }
  }
  &:nth-child(n + 4) {
    .rank {
      color: #888888;
    }
  }
  .textdec {
    width: 335rem;
    padding: 6px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    .left {
      width: 323rem;
      p {
        &:nth-child(1) {
          font-size: 17rem;
          color: #333333;
        }
        &:nth-child(2) {
          font-size: 13rem;
          color: #888888;
          i {
            display: inline-block;
            width: 12px;
            height: 8px;
            margin-right: 4px;
            background-image: url(@/assets/index_icon_2x.png);
            background-size: 166px 97px;
          }
        }
      }
    }
    .right {
      box-sizing: border-box;
      width: 42rem;
